.card {
  display: -ms-flexbox;
  display: flex;
  width: 40%;
  margin: auto;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Roboto', sans-serif;
  margin-top: 50px;
}

.card header {
  display: -ms-flexbox;
  display: flex;
  height: 20%;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-direction: row;
      flex-direction: row;
}

.card header img {
  width: 48px;
  height: 48px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 50%;
  margin-top: 2%;
  background-color: lightgrey;
}

.header-content-wrapper {
  margin-left: 3%;
}

.text-content {
  margin-top: 2%;
  font-size: 11pt;
}

.header-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin-top: 2%;
}

.user-name {
  font-weight: bold;
  font-size: 11pt;
}

.user-handle {
  margin-left: 6px;
  margin-right: 6px;
  color: grey;
  font-size: 10pt;
}

.post-date {
  color: grey;
  font-size: 10pt;
}

.content-box {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
}

.content-box img {
  width: 90%;
  height: 500px;
  margin: 3% 3% 3% calc(3% + 48px);
  border: solid lightgrey 1px;
  border-radius: 5px;
  background-color: lightgrey;
}

.card footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: grey;
  width: 60%;
  margin-left: calc(3% + 48px);
  -ms-flex-line-pack: center;
      align-content: center;
}

.card footer div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

#comment:hover {
  color: lightblue;
}

#retweet:hover {
  color: green;
}

#like:hover {
  color: red;
}

#mail:hover {
  color: darkblue;
}

#comment-clicked {
  color: lightblue;
}

#retweet-clicked {
  color: green;
}

#like-clicked {
  color: red;
}

#mail-clicked {
  color: darkblue;
}

.social-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

.social-button p {
  font-weight: bold;
  padding-left: 5px;
  font-size: 9pt;
}



